import React from "react";
import "./Skills.css";
import skillsData from "./SkillsData.json";

const Skills = () => {
  return (
    <div className="col-md-11 skills-content p-b-40">
      <h2 className="content-heading">Skills</h2>
      <p className="content-description">I can help you with</p>
      {Object.keys(skillsData).map(key => (<div key={key} className="skills-card-wrapper">
        <h4 className="font-light">{key}</h4>
        <div className="row text-center">
            {skillsData[key].map(item => (<div key={item.name} className="skills-card col-sm-4 col-md-2">
                <img alt="" src={`/assets/images/${item.image}`} className="img-responsive" />
                <span className="skill-text">{item.name}</span>
            </div>))}
        </div>
      </div>))}
    </div>
  );
};

export default Skills;
