import React from "react";
import { NavLink } from "react-router-dom";
import "./Sidebar.css";

const Sidebar = (props) => {
  return (
    <div className="col-md-1 side-nav">
      <ul>
        <li className="side-nav-item">
          <NavLink to="/" exact activeClassName="active">
            <span className="fas fa-user nav-item-icon" />
            <span>About Me</span>
          </NavLink>
        </li>
        <li className="side-nav-item">
          <NavLink to="/experience" activeClassName="active">
            <span className="fas fa-briefcase nav-item-icon" />
            <span>Experience</span>
          </NavLink>
        </li>
        <li className="side-nav-item">
          <NavLink to="/skills" activeClassName="active">
            <span className="fas fa-code nav-item-icon" />
            <span>Skills</span>
          </NavLink>
        </li>
        <li className="side-nav-item">
          <NavLink to="/projects" activeClassName="active">
            <span className="fas fa-code-branch nav-item-icon" />
            <span>Projects</span>
          </NavLink>
        </li>
        <li className="side-nav-item">
          <NavLink to="/education" activeClassName="active">
            <span className="fas fa-graduation-cap nav-item-icon" />
            <span>Education</span>
          </NavLink>
        </li>
        <li className="side-nav-item">
          <NavLink to="#" onClick={() => props.handleTerminalMode(true)} activeClassName="active">
            <span className="fas fa-terminal nav-item-icon" />
            <span>Terminal Mode</span>
          </NavLink>
        </li>
      </ul>
    </div>
  );
};

export default Sidebar;
