import React from "react";
import {
  VerticalTimeline,
  VerticalTimelineElement
} from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";
import "./Experience.css";

const Experience = () => {
  return (
    <div className="col-md-11 experience-content">
      <h2 className="content-heading">Work experience</h2>
      <p className="content-description">My previous jobs and internships</p>
      <div>
        <VerticalTimeline>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date="October 2018 - present"
            iconStyle={{
              background: "url(/assets/images/infostretch-logo.png)",
              backgroundSize: "cover"
            }}
          >
            <h4 className="vertical-timeline-element-title">
              Software Engineer
            </h4>
            <h5 className="vertical-timeline-element-subtitle">
              Infostretch, Ahmedabad
            </h5>
            <p>
              <ul className="achievements">
                <li>Winner of Infostretch Code On 2018 (Hackathon)</li>
                <li>Received Bright Spark award for Q3-2018</li>
                <li>Received Pat On Back awards for Q1-2018 and Q2-2018</li>
              </ul>
            </p>
          </VerticalTimelineElement>

          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date="July 2017 - September 2018"
            iconStyle={{
              background: "url(/assets/images/infostretch-logo.png)",
              backgroundSize: "cover"
            }}
          >
            <h4 className="vertical-timeline-element-title">
              Associate Software Engineer
            </h4>
            <h5 className="vertical-timeline-element-subtitle">
              Infostretch, Ahmedabad
            </h5>
            <p>
              <ul className="achievements">
                <li>Winner of Infostretch Code On 2018 (Hackathon)</li>
                <li>Received Bright Spark award for Q3-2018</li>
                <li>Received Pat On Back awards for Q1-2018 and Q2-2018</li>
              </ul>
            </p>
          </VerticalTimelineElement>

          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date="January 2017 - June 2017"
            iconStyle={{
              background: "url(/assets/images/intecons-logo.png)",
              backgroundSize: "cover"
            }}
          >
            <h4 className="vertical-timeline-element-title">
              Software Engineer Intern
            </h4>
            <h5 className="vertical-timeline-element-subtitle">
              Intecons Software Lab, Jaipur
            </h5>
            <p>
              <ul className="achievements">
                <li>Received award for developing and optimizing a real time chat feature in client project</li>
              </ul>
            </p>
          </VerticalTimelineElement>
        </VerticalTimeline>
      </div>
    </div>
  );
};

export default Experience;
