import React from "react";
import Terminal from 'terminal-in-react';

const TerminalComponent = (props) => {
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        height: "100vh",
        width: "100%"
      }}
    >
      <Terminal
        color="green"
        hideTopBar="true"
        backgroundColor="black"
        style={{ fontWeight: "bold", fontSize: "1em" }}
        commands={{
          open: (args, print) => {
            const key = args[1] ? args[1].slice(2) : "";
            let url = null;
            switch (key) {
              case "linkedin":
                url = "https://www.linkedin.com/in/in-hiteshaswani";
                break;
              case "portfolio":
                url = "http://hiteshaswani.in";
                break;
              case "github":
                url = "https://github.com/in-hiteshaswani";
                break;
              case "twitter":
                url = "https://twitter.com/in_hiteshaswani";
                break;
              case "facebook":
                url = "https://www.facebook.com/in.hiteshaswani";
                break;
              case "youtube":
                url =
                  "https://www.youtube.com/channel/UCFUSyiXOVmC7AsOMuXEOCAQ";
                break;
              case "instagram":
                url = "https://www.instagram.com/in.hiteshaswani";
                break;
              case "skype":
                url = "skype:hiteshaswani@hotmail.com";
                break;
              default:
                print(
                  "Argument passed is not valid. Please use the 'help' command to know more about the valid arguments."
                );
                break;
            }
            if (url) {
              window.open(url, "_blank");
            }
          },
          exit: () => props.handleTerminalMode(false)
        }}
        descriptions={{
          open:
            "opens social media profile. Valid arguments are:\n--linkedin\n--github\n--youtube\n--twitter\n--facebook\n--instagram\n--skype\n--portfolio",
          alert: "alert",
          popup: "alert",
          exit: "Exits the terminal mode and redirects to portfolio."
        }}
        msg="Welcome to terminal mode! Type 'help' to explore options. Type 'exit' to return to the website."
      />
    </div>
  );
};

export default TerminalComponent;
