import React from "react";
import {
  VerticalTimeline,
  VerticalTimelineElement
} from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";
import "./Projects.css";

const Projects = () => {
  return (
    <div className="col-md-11 project-content">
      <h2 className="content-heading">Projects</h2>
      <p className="content-description">
        My latest work, projects and developments
      </p>
      <div>
        <VerticalTimeline>
          <VerticalTimelineElement
            className="vertical-timeline-element--work project-border react-color"
            date="October 2018 - present"
            iconStyle={{
              textAlign: "center",
              lineHeight: "60px",
              backgroundColor: "#61DAFB",
              fontSize: "35px",
              color: "#fff",
              backgroundImage: "none"
            }}
            icon={<i className="fab fa-react" />}
          >
            <div className="tools-wrapper">
              <div className="tools-capsule">React 16</div>
              <div className="tools-capsule">Redux Saga</div>
              <div className="tools-capsule">Jest</div>
              <div className="tools-capsule">Google Maps</div>
            </div>
            <h4 className="vertical-timeline-element-title">
              Enterprise Multiple Currency Wallet Web Application
            </h4>
            <h5 className="vertical-timeline-element-subtitle">
              Infostretch Corporation Private Limited, Ahmedabad
            </h5>
            <p>
              It’s a web application which enables the users to have multiple
              currency accounts and use them to make payments and transfer money
              from one currency account to another. The users can either credit
              their wallet accounts through bank transactions or by earning
              loyalty points which they can redeem at several merchants/vendor
              stores integrated into this application. The loyalty points can be
              earned by availing the services of the registered vendors at
              regular periods of time. There is a nearby store locator
              functionality, which enables the users to find the physical stores
              nearby on google maps, where they can earn loyalty points by
              making a purchase.
            </p>
          </VerticalTimelineElement>

          <VerticalTimelineElement
            className="vertical-timeline-element--work project-border angular-color"
            date="April 2018 - September 2018"
            iconStyle={{
              textAlign: "center",
              lineHeight: "60px",
              backgroundColor: "#E43136",
              fontSize: "35px",
              color: "#fff",
              backgroundImage: "none"
            }}
            icon={<i className="fab fa-angular" />}
          >
            <div className="tools-wrapper">
              <div className="tools-capsule">Angular 6</div>
              <div className="tools-capsule">RxJS</div>
              <div className="tools-capsule">Material Design</div>
              <div className="tools-capsule">Jasmine</div>
            </div>
            <h4 className="vertical-timeline-element-title">
              Enterprise Questionnaire-Based Survey Web Application for Disease
              Diagnosis
            </h4>
            <h5 className="vertical-timeline-element-subtitle">
              Infostretch Corporation Private Limited, Ahmedabad
            </h5>
            <p>
              It is a web application which allows doctors/physicians to add
              surveys for patients using which they can diagnose their diseases.
              The application allows doctors to input several questions and set
              their display condition (in terms of the responses of previously
              displayed questions). The patients are also shown different
              responses like self-care instructions or immediate appointment
              with doctor, based on the answer sequence given by the patients.
              This application has a role-based user login system, in which
              doctors who have created the survey can provide read/edit/publish
              privileges to other doctors. The application also has features to
              export/import the existing surveys in Excel and JSON format. The
              application also allows the doctors to create new versions of a
              survey and archive older versions.
            </p>
          </VerticalTimelineElement>

          <VerticalTimelineElement
            className="vertical-timeline-element--work project-border angular-color"
            date="December 2017 - March 2018"
            iconStyle={{
              textAlign: "center",
              lineHeight: "60px",
              backgroundColor: "#E43136",
              fontSize: "35px",
              color: "#fff",
              backgroundImage: "none"
            }}
            icon={<i className="fab fa-angular" />}
          >
            <div className="tools-wrapper">
              <div className="tools-capsule">Angular 2</div>
              <div className="tools-capsule">RxJS</div>
              <div className="tools-capsule">QuaggaJS</div>
              <div className="tools-capsule">Jasmine</div>
            </div>
            <h4 className="vertical-timeline-element-title">
              Enterprise Health Monitoring Web Application
            </h4>
            <h5 className="vertical-timeline-element-subtitle">
              Infostretch Corporation Private Limited, Ahmedabad
            </h5>
            <p>
              It’s a web application which calculates the total calories intake
              of the patients based on the meal information provided by them and
              suggests them quantity of bolus dose of insulin to maintain normal
              sugar levels in their body. The patients can scan the barcode of
              the meal consumed by them or can input them manually.
            </p>
          </VerticalTimelineElement>

          <VerticalTimelineElement
            className="vertical-timeline-element--work project-border php-color"
            date="January 2017 - June 2017"
            iconStyle={{
              textAlign: "center",
              lineHeight: "60px",
              backgroundColor: "#777CB5",
              fontSize: "35px",
              color: "#fff",
              backgroundImage: "none"
            }}
            icon={<i className="fab fa-php" />}
          >
            <div className="tools-wrapper">
              <div className="tools-capsule">PHP</div>
              <div className="tools-capsule">MySQL</div>
              <div className="tools-capsule">jQuery</div>
              <div className="tools-capsule">PayPal</div>
            </div>
            <h4 className="vertical-timeline-element-title">
              Full House - Online Movie Ticket Booking Application
            </h4>
            <h5 className="vertical-timeline-element-subtitle">
              Jaipur National University (Major Project)
            </h5>
            <p>
              This project aimed at building a movie ticket booking platform
              where a customer can book movie tickets in various theatres in the
              city. It allows the customers to view the theatre-wise listing of
              various show times of a particular movie. It provides a graphical
              distribution of the theatre seats to ease the user to book the
              appropriate seat. Moreover, the seat chosen by the present
              customer is blocked for 2 minutes (until the customer completes
              his/her transaction), so that the same seat cannot be booked by
              any other customer simultaneously. It also has an ‘Admin Panel’
              having different user roles like ‘Admin’ and ‘Theatre Owner’,
              having their own access privileges to add/modify show timings,
              movie information, theatre screens and seat distribution etc.
            </p>
          </VerticalTimelineElement>
        </VerticalTimeline>
      </div>
    </div>
  );
};

export default Projects;
