import React, { Component } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import Terminal from './components/Terminal/Terminal';
import "./App.css";
import Sidebar from "./components/Sidebar/Sidebar";
import Home from "./components/Home/Home";
import Experience from "./components/Experience/Experience";
import Skills from "./components/Skills/Skills";
import Projects from "./components/Projects/Projects";
import Education from "./components/Education/Education";

class App extends Component {
  constructor(props) {
    super(props);
    this.handleTerminalMode = this.handleTerminalMode.bind(this);
    this.state = {
      isTerminalModeActive: false
    };
  }

  handleTerminalMode(state) {
    this.setState({ isTerminalModeActive: state });
  }

  render() {
    return (
      <React.Fragment>
        {!this.state.isTerminalModeActive ? (
          <div className="container-fluid">
            <div className="row">
              {/* Side Navigation Start */}
              <Sidebar handleTerminalMode={this.handleTerminalMode} />
              <div className="col-md-1" />

              {/* <!-- Main Content Start --> */}
              <Switch>
                <Route exact path="/" component={Home} />
                <Route exact path="/experience" component={Experience} />
                <Route exact path="/skills" component={Skills} />
                <Route exact path="/projects" component={Projects} />
                <Route exact path="/education" component={Education} />
                <Redirect to="/" />
              </Switch>

              {/* <!-- Main Content End --> */}
            </div>
          </div>
        ) : (
          <Terminal handleTerminalMode={this.handleTerminalMode} />
        )}
      </React.Fragment>
    );
  }
}

export default App;
